import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      searchinput: "",
      orderlist: ["ascending", "descending"],
      tableData: [],
      total: 0,
      rules: {
        act_name: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        type_id: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        status_id: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        act_info: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        start_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        end_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      form: {
        id: "",
        act_name: "",
        act_type: 1,
        act_status: 1,
        act_info: {},
        start_time: "",
        end_time: "",
        auth: "root"
      },
      currentPage: 1,
      limit: 10,
      visible: false,
      title: "添加系统公告",
      selectList: [],
      loading: false,
      titleConfig: "普通",
      ac_content: {
        //type_id = 1
        bg_url: "1",
        //type_id = 2
        jump_position: "2",
        jump_bt_url: "3",
        jump_bt_position: "4",
        //type_id = 3
        web_url: "5",
        //type_id = 4
        ac_desc: "6",
        task_list: [
          {
            task_type: "7",
            task_desc: "8",
            task_target: "9",
            task_awards: "10"
          }
        ],
        //type_id = 5
        earn_coins: "12",
        charge_target: "13",
        back_coins: "14",
        //type_id = 6
        advertise_url: "15",
        advertise_end_time: "16",
        award_list: [
          {
            rank_min: "17",
            rank_max: "18",
            award_coins: "19"
          }
        ],
        robot_update_time: "20",
        robot_update_rate: "21",
        robot_update_range: ["22", "23"]
        //type_id = 7
      },
      titleList: {
        1: "普通",
        2: "跳转",
        3: "跳转页面",
        4: "任务",
        5: "首冲活动",
        6: "排行榜活动",
        7: "绑定手机活动"
      },
      initForm: {
        id: "",
        act_name: "",
        act_type: 1,
        act_status: 1,
        act_info: "",
        start_time: "",
        end_time: "",
        auth: "root"
      }
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      title: this.searchinput
    });
  },
  methods: {
    //多选删除
    del() {
      //勾选需要删除的项目批量删除
      if (this.selectList.length != 0) {
        this.$confirm("确认删除吗?", "信息", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(async () => {
            let str = this.selectList.join();
            // console.log(str);
            let { data } = await this.$http.HallFunConfig.DeleteActivityList({
              id_list: `(${str})`
            });
            console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                title: this.searchinput
              });
            }

            this.$message({
              type: "success",
              message: "删除成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        this.$message("请选择需要删除的数据");
      }
    },

    //添加
    add() {
      this.editForm("添加活动", true, DeepData(this.initForm), "普通");
    },

    //发送到服务器配置
    send() {
      this.$confirm("确认发送吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(async () => {
        this.loading = true;
        let { data } = await this.$http.HallFunConfig.PostTableConfig({
          type_id: 7
        });
        // console.log(data);
        if (data.code === 1 && data.data) {
          this.loading = false;
          this.$message({
            type: "success",
            message: data.msg
          });
        }
      });
    },

    //搜索
    search() {
      if (this.searchinput === "") {
        this.$message({
          type: "warning",
          message: "请输入你要搜索的活动名称!"
        });
      }
      this.currentPage = 1;
      this.limit = 10;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //表格多选
    handleSelectionChange(sel) {
      let idList = sel.map(item => item.id);
      this.selectList = idList;
    },

    //页容量发生变化
    handleSizeChange(num) {
      this.currentPage = 1;
      this.limit = num;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //页码变化
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    upLoad() {},
    beforeAvatarUpload() {},
    Change(val) {
      Object.keys(this.titleList).forEach(item => {
        if (parseInt(item) === val) {
          this.titleConfig = this.titleList[val];
        }
      });
    },
    //type_id = 4的添加
    addConent() {
      this.ac_content.task_list.push({});
    },
    //type_id = 4的删除
    delConent(index) {
      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async () => {
          this.ac_content.task_list.splice(index, 1);

          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //type_id = 6的添加
    addAward() {
      this.ac_content.award_list.push({});
    },
    //type_id = 6的删除
    delAward(index) {
      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async () => {
          this.ac_content.award_list.splice(index, 1);

          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表格编辑
    handleEdit(row) {
      console.log(row);
      let editForm = DeepData(row);
      Object.keys(this.titleList).forEach(item => {
        if (this.titleList[item] === editForm.act_type) {
          this.titleConfig = editForm.act_type;
          editForm.act_type = parseInt(item);
        }
        editForm.act_status = editForm.act_status === "待上线" ? 1 : 2;
      });
      editForm.act_info = JSON.parse(editForm.act_info);
      this.ac_content = {...editForm.act_info}
      console.log(this.ac_content);
      console.log(editForm);
      
      this.editForm("更新活动", true, editForm, this.titleConfig);
    },

    //表格删除
    handleDelete(x, row) {
      console.log(x, row.id);

      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async () => {
          let { data } = await this.$http.HallFunConfig.DeleteActivityList({
            id: row.id
          });
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({
              page: this.currentPage,
              limit: this.limit,
              title: this.searchinput
            });
          }
          console.log(data);

          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit() {
      //表单验证
      this.$refs.form.validate(async valid => {
        if (valid) {
          //1.将form数据传递到paylist,新增到table中先显示一个dom
          console.log(this.form);
          // this.form.act_info =
          let { data } = await this.$http.HallFunConfig.PostActivityList(
            this.form
          );
          console.log(data);

          //2.发送请求追加数据到后台-------------------------------------------------------------

          // console.log("发送请求");

          //3.关闭新增的弹框
          this.editForm("添加活动", false, DeepData(this.initForm), "普通");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //表单返回
    back() {
      this.editForm("添加活动", false, DeepData(this.initForm), "普通");
    },

    editForm(title, visible, form, titleConfig) {
      this.title = title;
      this.visible = visible;
      this.form = form;
      this.titleConfig = titleConfig;
    },

    formateData(res) {
      res.forEach(item => {
        item.act_status = item.act_status === 1 ? "待上线" : "生效中";

        switch (item.act_type) {
          case 1:
            item.act_type = "普通";
            break;
          case 2:
            item.act_type = "跳转";
            break;
          case 3:
            item.act_type = "跳转网页";
            break;
          case 4:
            item.act_type = "任务";
            break;
          case 5:
            item.act_type = "首充活动";
            break;
          case 6:
            item.act_type = "排行榜活动";
            break;
          case 7:
            item.act_type = "绑定手机活动";
            break;

          default:
            break;
        }
      });
      return res;
    },

    // data(time) {
    //   let long1 = Date.parse(time);
    //   let long2 = new Date(long1).getTime();
    //   return long2;
    // },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetActivityList(params);
      // console.log(data);
      let localdata = this.formateData(DeepData(data.data));
      this.tableData = localdata;
      this.total = data.total;
      // console.log(localdata);
      // console.log(data);
    }
  }
};



